/* affects all header 1 elements: */
h1 {
    color: green;
    font-style: italic;
}
button:hover {
    color: blue;
    background-color: lightpink;
}
/* affects all elements with the className attribute set to "note" */
.note {
    color: grey;
    padding-top: 5px;
    font-size: 15px;
}
.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    font-weight: bold;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}
